.collect_result_container {
  width: 83vw;
  margin: auto;
  display: flex;
  color: white;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  text-align: center;
}
h1 {
  font-size: 2.5em;
}
p {
  color: grey;
}
.collect_result {
  margin-bottom: 50px;
}
.collect {
  width: 42vw;
  border: 1px solid grey;
  padding: 10px;
  display: flex;
  flex-direction: column;
  height: 65vh;
  align-items: flex-start;
  margin-left: 50px;
}
.message {
  font-weight: 200;
  font-size: 14px;
  text-align: left;
  background-color: rgba(55, 69, 147, 0.498);
  padding: 10px;
  border-radius: 5px;
  display: inline-block;
  max-width: 70%; /* Adjust max-width as needed */
  min-width: 50px; /* Adjust min-width as needed */
  margin: 5px 50px;
}
