.login_container {
  background-color: #18181b;
  height: 100vh;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
}
.all_shapes {
  position: absolute;
}
.circle1 {
  bottom: 0;
  right: 15em;
  width: 16vw;
}
.circle2 {
  right: 0;
  top: 3em;
  width: 8vw;
}
.tri {
  left: 1em;
  width: 22vw;
}
.login_form {
  display: flex;
  width: 30vw;
  flex-direction: column;
}
.login_input {
  color: grey;
  border: 1px solid grey;
  background: none;
  font-size: 16px;
  border-radius: 10px;
  padding: 12px;
  margin-top: 10px;
  margin-bottom: 10px;
}
.login_button {
  border-radius: 10px;
}
.back_icon {
  position: absolute;
  top: 50px;
  font-size: 30px;
  left: 3em;
  &:hover {
    transform: scale(1.2);
  }
}
.error {
  color: red;
}
@media only screen and (max-width: 768px) {
  .login_form {
    width: 80vw;
    padding: 0 5vw;
  }
  .circle1,
  .circle2,
  .tri {
    display: none;
  }

  .back_icon {
    top: 20px;
    left: 20px;
  }
}
