.footer {
  display: flex;
  justify-content: space-evenly;
  align-items: flex-start;
  color: white;
  padding-top: 15px;
  margin: auto;
  width: 83vw;
  padding-bottom: 35px;
}

.footer-section {
  display: flex;
  flex-direction: column;
  text-decoration: underline;
}

.footer-section p {
  margin: 0;
  padding: 0 0 10px 0;
}

.footer-section a {
  color: #bbbbbb;
  text-decoration: none;
  margin: 5px 0;
}

.footer-section a:hover {
  text-decoration: underline;
}
