.all_container {
  padding-top: 50px;
  width: 100vw;
  background: #171923;
}
.navbar_container {
  display: flex;
  margin: auto;
  text-align: center;
  width: 83vw;
  justify-content: space-between;
}

.sign-up {
  padding: 8px;
  margin-right: 10px;
  color: rgb(46, 46, 254);
  background-color: transparent;
  border: 1px solid rgb(46, 46, 254);
  border-radius: 5px;
  &:hover {
    background-color: rgb(46, 46, 254);
    color: white;
  }
}
.create_formbot {
  padding: 8px;
  margin-right: 10px;
  background-color: rgb(46, 46, 254);
  border-radius: 5px;
  border: none;
  color: white;
  &:hover {
    background-color: rgb(255, 255, 255);
    color: rgb(46, 46, 254);
  }
}
@media only screen and (max-width: 768px) {
  .all_container {
    padding-top: 30px;
  }

  .navbar_container {
    width: 95vw;
    flex-direction: column;
    align-items: center;
  }

  .sign-up,
  .create_formbot {
    margin: 5px 0;
    width: 100%;
    max-width: 200px;
  }
}
