.chat-container {
  display: flex;
  flex-direction: column;
  height: 100vh;
  padding: 10px;
  background-color: #2c2c2c;
  color: white;
}

.messages {
  flex: 1;
  overflow-y: auto;
  padding: 10px;
}

.message {
  margin-bottom: 10px;
  /* position: relative; */
}

.message.other .message-content {
  background-color: #eaeaea;
  color: black;
  padding: 10px;
  border-radius: 5px;
  display: inline-block;
}

.message.user .message-content {
  background-color: #007bff;
  color: white;
  padding: 10px;
  border-radius: 5px;
  display: inline-block;
  align-self: flex-end;
}

.input-container {
  display: flex;
  padding: 10px;
  background-color: #1f1f1f;
}

.input-container input {
  flex: 1;
  padding: 10px;
  border: none;
  border-radius: 5px;
  margin-right: 10px;
}

.input-container button {
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  background-color: #007bff;
  color: white;
  cursor: pointer;
}

.hi-button {
  position: absolute;
  right: 50px;
  top: 100px;
  transform: translateY(-50%);
  padding: 10px 20px;

  border-radius: 5px;
  background-color: #007bff;
  color: white;
  cursor: pointer;
}
.hi-button.clicked {
  background-color: orange;
}

@media (max-width: 768px) {
  .chat-container {
    padding: 5px;
  }

  .message .message-content {
    padding: 5px;
    border-radius: 3px;
  }

  .input-container input,
  .input-container button,
  .hi-button {
    padding: 5px;
    border-radius: 3px;
  }

  .hi-button {
    right: 50px;
  }
}
