.Improve_container {
  height: 100vh;
  width: 100vw;
  margin: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #0e0e1e;
  color: white;
  font-family: Arial, sans-serif;
}

.background {
  width: 80%;
  max-width: 800px;
  text-align: center;
}

.shape {
  position: relative;
  border: none;
}
.triangle {
  position: absolute;
  left: -15em;
  width: 250px;
  top: -10em;
}
.semicircle {
  position: absolute;
  right: -10em;
  bottom: -10em;
  width: 180px;
}

.content {
  position: relative;
  /* background: #162447; */
  padding: 20px;
  border-radius: 8px;
  /* box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2); */
}

h1 {
  font-size: 1.5em;
  margin: 0;
  padding: 10px 0;
}

button {
  background: #007bff;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 4px;
  cursor: pointer;
  margin: 10px 0;
  font-size: 1em;
}

button:hover {
  background: #0056b3;
}

p {
  margin: 0;
  color: #b0b0b0;
}
